.price {
    margin-bottom: 100px;
  position: relative;
}

.form {
    max-width: 420px;
}
.price__inner {
    position: relative;
}
.form__title {
    margin-bottom: 40px;
    font-weight: 700;
font-size: 16px;
line-height: 19px;
text-transform: uppercase;

}

.form__text{ 
    max-width: 300px;
    margin-bottom: 30px;
}

.form__input-name, .form__input-phone {
    border: none;
    width: 100%;
    border-bottom: 1px solid #131313;
    padding-bottom: 10px;
    margin-bottom: 20px;
    /* когда клацаем на input появляется прямоугольные границы черного цвета поэтом чтоб их убрать  */
    outline: none;

}
.form__input-name::placeholder, .form__input-phone::placeholder, .form__input-text {
    color: #6F6F6F;
    opacity: 0.5;
}
.form__input-text {
margin-bottom: 10px;
}


/* radio button т.е. кружочки нужно спрятать но просто display:none это не хорошая практика */
.form__input-radio {
    position: absolute;
    width: 1px;
    overflow: hidden;
    height: 1px;
    clip: rect(0 0 0 0);
}
.form__bike {
    
    position: absolute;
    top: 0;
    right: 0;
}
.form__input-radio:checked + .form__bike {
    display: block;
}


.form__label-box {
display: flex;
margin-bottom: 50px;
} 
.form__label +.form__label {
margin-left: 23px;
}



.form__radio-style {
    border: 1px solid #6F6F6F;
    width: 80px;
height: 12px;
border-radius: 30px;
cursor: pointer;

}

.form__btn {
    background: #131313;
    border: none;
    color: #fff;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 24px;
padding: 28px 71px 28px 81px ; 
letter-spacing: 1.1em;
transition: all 0.4s;


}

.form__btn:hover {
    background-color: #FF4040;
}

.form__bike-price {
       position: absolute;
    font-size: 32px;
line-height: 39px;
top: 30px;
left: 0;
    right: 0;
    width: 220px;
    height: 100px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #FF4040;
    border-radius: 50%;
    transform: translateX(-50%);
}
.form__bike-price::after {
    content: "";
    position: absolute;
    width: 220px;
    height: 100px;
    border: 1px solid #FF4040;
    border-radius: 50%;
    top: 12px;
 
}
@media(max-width:1150px) {
    .price {
        margin-bottom: 50px;
    }
    .form__bike-img {
        margin-top: 45px ;
        width: 500px;
    }
}


@media(max-width:991px) {
 .form__label-box {
     margin-bottom: 300px;
 }

 .form__bike-img {
margin-top: 10px;
    width: 430px;
}


 .form__bike {
     top: auto;
     bottom: 78px;
     left: 0;
     right: auto;
 }
 .form__bike-price {
top: 0;
 }
 .form__bike-price {
     right: -20%;
    transform: translateX(100%);
    left: auto;
 }
}

@media(max-width:760px) {
    .form__bike-price {
        transform: translateX(40%);
    }
   }



   @media (max-width: 624px) {
    .form__bike-price {
     font-size: 18px;
     width: 128px;
height: 69px;
transform: translateX(-40%);

    }

    .form__bike-price::after {
        
        width: 128px;
   height: 69px;
       }


 }
 @media (max-width: 490px) {
    .form__bike-price {
      left: 0;
      right: 0;
      transform: translateX(0%);
    }
    .form__bike-img {
     
    }
 }

   @media (max-width: 460px) {
   .form__btn {
       width: 100%;
     letter-spacing: 0.8em;
     padding: 28px 15px;
   }

   .form__bike-img {
     width: 350px;
}
.form__bike-price {
    transform: translateY(-70%) translateX(82%);
}



}

@media (max-width: 400px) {
   
 
    .form__bike-img {
      width: 300px;
 }

 
 
 }


