
.modal  {
    color: #fff;
    display: block;
    position: absolute;
    max-width: 400px;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    
   
    background-color: rgb(11, 11, 11);
    z-index: 555;
}


.modal__header {
    text-align: right;
    cursor: pointer;
    font-size: 25px;
    padding: 5px 15px;
}

.modal__title {
font-size: 16px;
text-align: center;

}
.modal__inner {
    margin: 10px 20px ;
}

.modal__bike { 

display: flex;
justify-content: space-around;
align-items: center;
}
.modal__color {
    width: 50px;
    height: 50px;;
}
@media(max-width: 1100px) {
    .modal  {
        
        left: 0;
        right: 0;
        top: 250px;
        bottom: 0;
        height: 250px;
      
}
}

@media(max-width: 420px) {
    .modal__bike { 
        flex-direction: column;
    }

    .modal__bike > *+* {
margin-top: 20px;
    }

    .modal {
        margin: 10px 10px;
    }
}