.header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu {
    margin-right: 70px;
}
.menu__list {
    display: flex;

}
.menu__item + .menu__item {
padding-left: 10px;
}
.menu__link {
text-transform: uppercase;
padding: 43px 20px;
transition: all 0.3s;
}
.menu__link:hover {
    background-color: #131313;
    color: #fff;
    
}

@media (max-width: 991px) {
.menu {
    margin-right: 0;
}
}

@media (max-width: 640px) {
    .header__inner {
        display: block;
        text-align: center;
    }
    .menu {
        margin-top: 50px;
    }
    .menu__list {
        justify-content: space-evenly;
    }
.menu__item + .menu__item{
    padding-left: 0;
}
.menu__item  {
    padding: 25px 20px ;
}


.menu__list {
    justify-content:space-between;
 
}

    }


    @media (max-width: 520px) {
        .menu__link {
            padding: 25px 10px;
        }
     }



    @media (max-width: 460px) {
   .menu {
       margin-top: 10px;
   }
       

        .menu__item  {
            padding: 25px 0px ;
            font-size: 12px;
        }

        .menu__link {
            padding: 21px 5px;
        }
        }