.bike {
    padding: 100px 0 150px;
}

.bike__inner {
    display: flex;
    justify-content: space-between;
}
.bike__column-slider {
    /* если мы используем флексы то жалательней использовать вместо width:  flex-basis: ; */
    flex-basis: 630px;
    margin-left: 30px;
    max-width: 630px;
}

.bike__slider {
    /* width: 630px; */
}

.bike__title {
    font-weight: 700;
font-size: 64px;
line-height: 77px;
margin: 80px 0 5px;
}

.bike__subtitle {
    font-weight: 400;
    font-size: 32px;
line-height: 39px;
color: #6F6F6F;
margin: 0 0 60px;
}

.bike__text {
    margin-bottom: 20px;
   /* почему здесь не flex-basis, потому что этот элемент уже конкретно глубоко и потомок потомков, короче у него нету родителя display:flex: ; */
    max-width: 420px;
}

.bike__link {
  margin-top: 80px;
  /* т.к. по умолчанию ссылки строчные и к ним не применяются margin */
  display: inline-block;
  font-size: 20px;
line-height: 24px;
letter-spacing: 1.1em;
text-transform: uppercase;
color: #FFFFFF;
padding: 28px 80px;
background: #131313;
transition: all 0.3s ease-out;
}

.bike__link:hover {
    background: #FF4040;
}

.slick-dots {
top:50px;
}

 .slick-dots {
    position: absolute;
    display: flex !important;
    flex-direction: column;
    justify-content: start;
    align-items: end;
}


 .slick-dots li + li {
margin-top: 10px;
 }
 .slick-dots li button {
     display: block;
    width: 2px;
    height: 25px;
    background: #131313;
    border: none;
border-radius: 7px;
padding: 0;

 }
 .slick-dots li button::before {
     content: "";
 }
 .slick-dots li button:hover,  .slick-dots .slick-active button {
    background: #FF4040;
    transition: all 0.3s;
 }


 .bike__list {
     margin-top: 25px;
     display: flex;
     justify-content: space-around;
 }

 .bike__list-item {
       min-width: 160px;
       display: flex;
       flex-direction: column;
       justify-content: space-between;
       align-items: center;
       text-align: center;
       min-height: 58px;
  }

 .bike__list-num {
    font-weight: 700;
    text-transform: uppercase;
   
}


 .bike__list-text {
font-size: 12px;
font-weight: 400;
line-height: 15px;
margin: auto 0;

 }
@media(max-width: 1250px) {
    .bike {
        padding-top: 0px;
        padding-bottom: 50px;
    }
.bike__inner {
   flex-wrap: wrap;
 
}
.bike__text {
    max-width: 100%;
}
.bike__link {
    margin-top: 30px;
}
.bike__column {
    margin-bottom: 30px;
}

.bike__column-slider {
    max-width: 100%;
    margin: 0;
}

}
@media(max-width: 640px) {
    .bike__title, .bike__subtitle {

        text-align: center;
    }

.bike__link {
    display: block;
    max-width: 100%;
    text-align: center;
}
.bike__title {
    margin-top: 20px;
}

.bike__list {
    flex-wrap: wrap;
}

.bike__list-item {
    width: 50%;
}
}

@media(max-width: 460px) {
    .bike__link {
        letter-spacing: 0.8em;
        padding: 28px 15px
    }
}