*, *::after, *::before {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #131313;
}


.container {
  max-width: 1220px;
  padding: 0 10px;
  margin: 0 auto;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
li {
  padding-left: 0;

}
a {
text-decoration: none;
color: inherit;
  }

  p {
    margin: 0;
  }