.characteristics {
    margin-bottom: 150px;
}


.characteristics__maintitle {
    margin: 0 0 40px;
    font-weight: 700;
font-size: 16px;
line-height: 19px;
text-transform: uppercase;
}
.characteristics__inner {
    display: flex;
}

.characteristics__img {
    /* object-fit: contain; */
    flex-basis: 570px;
    align-self: center;
}
.characteristics__items {
    flex-basis: 570px;
    margin-right: 60px;
}

.characteristics__list +.characteristics__list {
    padding-top: 30px;
}
.characteristics__title {
    padding: 2px 0 3px;
    text-transform: uppercase;
    color: #fff;
    background-color: #131313;
    font-weight: 700;

}
.characteristics__line {
    padding-top: 5px;
    display: flex;
}


.characteristics__name {
    flex-basis: 285px;
    padding-right: 25px;
}
.characteristics__desc {
    flex-basis: 285px;
}
@media( max-width: 1000px) {
    .characteristics__inner {
        flex-wrap: wrap;
    }
    .characteristics__img {
        order: -1;
    flex-basis: 100%;
    }
    .characteristics__items {
        flex-basis: 100%;
        margin-right: 0px;
    }
    .characteristics__desc {
        flex-basis: 50%;
    }
    .characteristics__name {
        flex-basis: 50%;
    }
    .characteristics {
        margin-bottom: 50px;
      }

}

@media( max-width: 590px) {
    .characteristics__img {
        display: none;
    }
}