.slider {
    margin-bottom: 50px;
}

.slider__item {
    padding-left: 50px;
    position: relative;
}


.slider__items .slick-dots {
    
    justify-content: start;
    align-items: start;
   
}

.slider__items .slick-dots {
    top: 0;
}

@media( max-width: 1250px) {
  .slider__img {
      object-fit: cover;
  }
}

@media( max-width: 991px) {
    .slider__img {
       height: 500px;
    }
}
    @media( max-width: 880px) {
        .slider__img {
           height: 400px;
        }
    }

    @media( max-width: 720px) {
        .slider__img {
           height: 300px;
        }
        .slider__item {
                padding-left: 10px;
        }
    }

    @media( max-width: 525px) {
        .slider__img {
           height: 200px;
        }
    }
        @media( max-width: 390px) {
            .slider__items  {
               display: none;
            }
       
    }